@mixin breakpoint($point) {
  @if $point == xs {
    @media (max-width: 360px) { @content; }
  }
  @else if $point == sm {
    @media (max-width: 575px) { @content; }
  }
  @else if $point == md {
    @media (max-width: 767px) { @content; }
  }
  @else if $point == lg {
    @media (max-width: 991px) { @content; }
  }
  @else if $point == xl {
    @media (max-width: 1200px) { @content; }
  }
}
